.geometry-drawing-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.geometry-drawing-panel {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  width: 95vw;
  max-width: 900px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 8px 8px 0 0;

  h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
    color: #333;
  }

  .close-btn {
    background: none;
    border: none;
    padding: 4px;
    cursor: pointer;
    color: #666;
    font-size: 16px;
    border-radius: 4px;

    &:hover {
      background: #e9ecef;
      color: #333;
    }
  }
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-gap: 20px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;

  h4 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    color: #555;
  }

  .canvas-actions {
    display: flex;
    gap: 8px;
  }
}

// 왼쪽 컬럼 - 도구와 설정
.tools-section, .style-section, .shapes-section {
  margin-bottom: 24px;
  
  .section-header {
    border-bottom: 2px solid #e9ecef;
    padding-bottom: 8px;
    margin-bottom: 16px;
  }
}

.tool-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;

  button {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    padding: 12px 8px;
    border: 2px solid #e0e0e0;
    border-radius: 6px;
    background: white;
    cursor: pointer;
    transition: all 0.2s;
    font-size: 12px;

    &:hover {
      border-color: #007bff;
      background: #f8f9ff;
    }

    &.active {
      border-color: #007bff;
      background: #e3f2fd;
      color: #007bff;
    }

    i {
      font-size: 20px;
    }

    span {
      font-weight: 500;
    }
  }
}

.style-controls {
  .control-group {
    margin-bottom: 20px;

    label {
      display: block;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 500;
      color: #555;
    }
  }

  .color-picker-group {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .color-input {
      width: 60px;
      height: 40px;
      border: 1px solid #ccc;
      border-radius: 4px;
      cursor: pointer;
    }

    .color-presets {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 4px;

      .color-preset {
        width: 30px;
        height: 30px;
        border: 2px solid #e0e0e0;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          transform: scale(1.1);
          border-color: #007bff;
        }

        &.active {
          border-color: #007bff;
          box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
        }
      }
    }
  }

  .stroke-width-options {
    display: flex;
    gap: 4px;
    flex-wrap: wrap;

    button {
      padding: 8px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;
      font-size: 12px;

      &:hover {
        border-color: #007bff;
        background: #f8f9ff;
      }

      &.active {
        border-color: #007bff;
        background: #007bff;
        color: white;
      }
    }
  }

  .fill-toggle {
    margin-bottom: 12px;

    .checkbox-label {
      display: flex;
      align-items: center;
      gap: 8px;
      cursor: pointer;
      font-size: 14px;

      input[type="checkbox"] {
        width: 16px;
        height: 16px;
      }
    }
  }

  .fill-color-group {
    margin-top: 12px;
  }
}

// 오른쪽 컬럼 - 캔버스
.canvas-section {
  .canvas-actions {
    button {
      padding: 6px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;
      font-size: 12px;

      &:hover:not(:disabled) {
        border-color: #007bff;
        background: #f8f9ff;
      }

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }

      &.undo-btn {
        color: #007bff;
        
        &:hover:not(:disabled) {
          background: #e3f2fd;
        }
      }

      &.clear-btn {
        color: #dc3545;
        
        &:hover:not(:disabled) {
          background: #f8d7da;
        }
      }

      i {
        margin-right: 4px;
      }
    }
  }
}

.canvas-container {
  position: relative;
  border: 2px solid #e0e0e0;
  border-radius: 6px;
  background: white;
  height: 400px;

  .drawing-canvas {
    width: 100%;
    height: 100%;
    cursor: crosshair;
    border-radius: 4px;
  }

  .canvas-help {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #999;
    pointer-events: none;

    p {
      margin: 0;
      font-size: 14px;
    }
  }
}

.shapes-list {
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid #e0e0e0;
  border-radius: 4px;

  .shape-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    border-bottom: 1px solid #f0f0f0;

    &:last-child {
      border-bottom: none;
    }

    .shape-info {
      display: flex;
      align-items: center;
      gap: 8px;

      i {
        color: #666;
        width: 16px;
      }

      span {
        font-size: 14px;
        color: #333;
      }
    }

    .shape-preview {
      display: flex;
      align-items: center;
      gap: 6px;

      .color-preview, .fill-preview {
        width: 16px;
        height: 16px;
        border: 1px solid #ccc;
        border-radius: 2px;
      }

      span {
        font-size: 12px;
        color: #666;
      }
    }
  }
}

.panel-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-top: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 0 0 8px 8px;

  .footer-info {
    font-size: 14px;
    color: #666;
  }

  .footer-actions {
    .cancel-btn {
      padding: 8px 16px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      color: #666;
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background: #f8f9fa;
        border-color: #999;
      }
    }
  }
}

// 반응형 디자인
@media (max-width: 1024px) {
  .panel-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  
  .canvas-container {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .geometry-drawing-panel {
    width: 98vw;
    max-height: 95vh;
  }

  .tool-buttons {
    grid-template-columns: repeat(4, 1fr);
    
    button {
      padding: 8px 4px;
      
      span {
        font-size: 10px;
      }
    }
  }

  .canvas-container {
    height: 250px;
  }

  .panel-content {
    padding: 16px;
  }
}